<template>
   <div>
      <div class="flex justify-end items-center">
         <button 
         class="inline-flex border border-transparent bg-red rounded-md font-semibold text-xs uppercase text-white px-4 py-2 mr-auto" 
         @click="deleteRows"
         v-if="isAdmin"><i class="fa fa-trash mx-1"></i></button>
         <label for="filter">Filtro: </label>
         <input id="filter" 
         class="form-control border-b my-4 px-3 no-underline
         focus:outline-none" 
         v-model="filters.alias.value"/>
      </div>
      <v-table 
         :data="rows"
         :filters="filters"
         :currentPage.sync="currentPage"
         :pageSize="25"
         @totalPagesChanged="totalPages = $event">
         <thead slot="head" class="uppercase text-white">
            <th v-if="isAdmin"><input type="checkbox" v-model="all" @click="clearSelection"></th>
            <th v-for="(col, index) in columns" :key="index">{{ col }}</th>
            <th>actions</th>
         </thead>
         <tbody slot="body" slot-scope="{displayData}">            
            <tr v-for="(row, i) in displayData" :key="row.id" :class="{'bg-row': (i%2 > 0)}">
               <td v-if="isAdmin"><input type="checkbox" :value="row.id" :checked="all" @click="getitemToDelete(row.id, $event)"></td>
               <td v-for="(col, index) in columns" :key="index">   
                  <span class="text-green" v-if="(typeof row[col] == 'boolean' && row[col])"><i class="fas fa-check"></i></span>
                  <span class="text-red" v-if="(typeof row[col] == 'boolean' && !row[col])"><i class="fas fa-minus-circle"></i></span>
                  <span v-if="(typeof row[col] != 'boolean')">{{ row[col] }}</span>                  
               </td>
               <td>
                  <button class="ui small button" 
                  @click="edit(row)">
                     <i class="fa fa-edit text-indigo-700 hover:opacity-50 m-2"></i>
                  </button>    
                  <button class="ui small button" 
                  @click="deleteRow(row.id)">
                     <i class="fa fa-trash text-red-600 hover:opacity-50 m-2"></i>
                  </button>     
               </td>               
            </tr>
         </tbody>
      </v-table>

      <smart-pagination v-if="totalPages > 0"
        :currentPage.sync="currentPage"
        :totalPages="totalPages"
      />
   </div>
</template>
<script>
//https://tochoromero.github.io/vuejs-smart-table/the-basics/#table
import authService from "@/services/user/auth.service"

export default { 
   props: {
      rows: [],      
   },
   name: 'AdminTable',
   data() {
      return {
         isAdmin: false,
         columns: [],
         tableInfo: [],         
         filters: {
            alias: { value: '', keys: ['alias'] }
         },
         all: false,
         items_id: [],
         currentPage: 1,
         totalPages: 0
      }
   },
   created() {
      this.isAdmin = authService.validateRole('ROLE_SuperAdmin')
      this.initTable()
   },  
   watch: {
      rows() {
         this.initTable()
      }
   }, 
   methods: {
      initTable() {                       
         if (this.rows != undefined){                        
            this.columns = Object.keys(this.rows[0])            
            if (this.columns[0] == 'id') this.columns.shift()            
            // this.totalPages = 25%this.rows.length
         }
      },    
      edit(rowData) {
         this.$emit('editData', rowData)         
      },
      clearSelection() {
         if (!this.all) {
            this.rows.map(e => {
               this.items_id.push(e.id)
            })
         } else this.items_id = []
      },
      getitemToDelete(item_id, e) {
         if(e.target.checked) {
            if(this.items_id.findIndex(i => i == item_id) == -1) {
               this.items_id.push(item_id)
            }
         } else {
            const del = this.items_id
            this.items_id = []
            del.map(i => {
               if(i != item_id)
                  this.items_id.push(i)
            })
         }
      },      
      deleteRow(rowData) {
         this.items_id = [rowData]         
         this.deleteRows()         
      },
      deleteRows() {
         this.$emit('deleteData', this.items_id)
      },      
   }
}
</script>
<style scoped>

</style>