import Authorization from "../user/auth.service";

const CurrencyService = {
    getList() {
        try {
            return Authorization.checkLogin(`/currency`).then(res => {
                const list = []
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            id: item.id,
                            name: item.name,
                            code: item.code,
                            active: (item.active) ? true : false,
                            //iso_code: item.iso_code,
                            //symbol: item.symbol,
                            //rate: item.rate,
                        })
                    }
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    },
    async getCurrency(id) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/currency/${id}`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },
    async addUpdCurrency(currency_data, is_new = true) {
        try {
            currency_data.auth = 'bearer'
            const { status, data } = (is_new) ?
            await Authorization.checkLogin('/currency/create', currency_data, 'post'):
                await Authorization.checkLogin(`/currency/upd/${currency_data.id}`, currency_data, 'put')
            if (status == 200) {
                return data
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
        return false
    },
    async delete(ids) {
        const delete_data = {
            auth: 'bearer',
            ids
        }
        try {
            const { status, data } = await Authorization.checkLogin('/currency/delete', delete_data, 'post')
            if (status == 200) {
                return this.getList().then(list => {
                    return list
                })
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
    }
}

export default CurrencyService